.container {
  background-color: #333333;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 20px;
  box-sizing: border-box;
  width: 100%;
}

.textbox {
  background-color: #333333;
  text-align: center;
  padding: 20px;
  margin-bottom: 20px;
  border-radius: 10px;
  width: 60vw;
  color: #8be085;
}

body {
  margin: 0;
  padding: 0;
  background-color: black;
  color: white;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 18px;
  overflow-y: scroll; /* Enable vertical scrollbar */
}

.main-images {
  width: 40%;
  height: auto; /* Set your desired maximum height */
  
}

.hidden {
  display: none;
  width: 40%;
  height: auto; /* Set your desired maximum height */
  
}


.App-link {
  color: #61dafb;
}

/* width */
::-webkit-scrollbar {
  width: 12px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #4b4b4b;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 6px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

@media (max-width: 768px) and (orientation: portrait) {
  .main-images {
    width: 90%; /* Adjust width for smaller screens */
  }
}